.checkmark__circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 0.1;
  stroke-miterlimit: 0.1;
  stroke: black;
  fill: white;

  animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}
.checkmark {
  width: 286px;
  height: 286px;
  border-radius: 50%;
  display: block;
  stroke-width: 0.5;
  stroke: black;
  stroke-miterlimit: 10;
  margin: 10% auto;
  /* box-shadow: inset 0px 0px 0px black; */
  animation: fill 10s ease-in-out 10s forwards, scale 0.3s ease-in-out 0.9s both;
}
.checkmark__check {
  transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
}
@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes scale {
  0%,
  100% {
    transform: none;
  }
  50% {
    transform: scale3d(1.1, 1.1, 1);
  }
}
@keyframes fill {
  /* 100% {
    box-shadow: inset 0px 0px 0px 10px black;
  } */
}
